<script>
import recentJobs from '../data/recentJobs.json';
import education from '../data/education.json';

export default {
    data() {
        return {
            recentJobs,
            education,
            posts: []
        }
    },
    mounted() {
        this.getMediumFeeds();
    },
    methods: {
        getMediumFeeds(){
            const baseURI = 'https://api.rss2json.com/v1/api.json?rss_url=https://medium.com/feed/@geekofer'
            this.$http.get(baseURI)
            .then((response) => {
                this.posts = response.data.items;
            })
        }
    }
}
